<template>
    <!--=== blog page ===-->
    <section class="blog-posts">
        <div class="container">
            <div class="row">
                <div class="col">
                    <div class="row">
                        <div class="col-12">
                            <div class="section-header">
                                <div class="sec-icon"><i class="ion ion-briefcase"></i></div>
                                <div class="sec-title">Product hall</div>
                            </div>
                        </div>
                            <div class="col-lg-4 col-md-6 col-sm-6" v-for="(item,index) in shopList" :key="index" >
                                <div class="blog-post">
                                    <div class="post-img">
                                        <img class="img-fluid" :src="imgUrl+item.path" alt="image">
                                    </div>
                                    <div class="blog-details">
                                        <a href="javascript:;"><h4 class="media-heading">{{item.name}}</h4></a>
                                        <p>{{item.type_descr}}</p>
                                    </div>
                                </div>
                            </div>
                    </div>
                </div>
            </div>
        </div>
        <comment />
    </section>
</template>

<script>
    import comment from '../common/comment'
    export default {
        name: "shop",
        components:{
            comment
        },
        mounted(){
            this.getShopList();
        },
        data:()=>({
            shopList:[]
        }),
        methods:{
            getShopList(){
                this.loading();
                let _self = this;
                _self.$ajax.post('/api/getgoods',{}).then(res=>{
                    if(res.resultCode === 1){
                    _self.shopList = res.resultData;
                        this.closeLoading();
                    }
                })
            },
        }
    }
</script>

